@font-face {
    font-family: "reg";
    src: url("../fonts/RB-Regular.ttf") format("ttf"),
        url("../fonts/RB-Regular.woff") format("woff"),
        url("../fonts/RB-Regular.woff2") format("woff2"),
        url("../fonts/RB-Regular.eot") format("eot");
}

@font-face {
    font-family: "bold";
    src: url("../fonts/RB-Bold.ttf") format("ttf"),
        url("../fonts/RB-Bold.woff") format("woff"),
        url("../fonts/RB-Bold.woff2") format("woff2"),
        url("../fonts/RB-Bold.eot") format("eot");
}
