.Flex {
    display: flex;
    align-items: center;
    justify-content: center;
}

.hover {
    transition: .5s;

    &:hover {
        transform: translateY(-5px);
    }
}

@keyframes pulse {
    0% {
        opacity: 1;
    }

    50% {
        opacity: .6
    }

    100% {
        opacity: 1;
    }
}