@import "main";
// End Of Import

.loader {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    @extend .Flex;
    background-color: #FFF;
    z-index: 999999;

    img {
        animation: pulse 2s linear infinite;
    }
}

.navover {
    position: fixed;
    width: 100%;
    height: 100%;
    background-color: rgba($color-sec, 0.95);
    z-index: 9;
    transition: .5s;
    opacity: 0;
    visibility: hidden;

    &.open {
        opacity: 1;
        visibility: visible;
    }
}

.sideNav {
    position: fixed;
    top: 0;
    right: 0;
    width: 100%;
    height: 100%;
    background-color: $color-sec;
    z-index: 99;
    overflow-y: auto;
    transition: .5s;
    @extend .Flex;
    flex-direction: column;
    transform-origin: right;
    transform: scaleX(0);

    html[dir="ltr"] & {
        transform-origin: left;
    }

    &.open {
        // transform-origin: left;
        transform: scaleX(1);

        ul {
            opacity: 1;

            li {
                transform: translateX(0);
                opacity: 1;
            }
        }
    }

    &.origin {
        transform-origin: left;

        html[dir="ltr"] & {
            transform-origin: right;
        }
    }

    .options {
        padding: 0 50px;
        display: flex;
        align-items: center;
        justify-content: space-between;
        width: 100%;
        position: absolute;
        top: 50px;

        @include mob480 {
            padding: 0 40px;
            top: 30px;
        }

        .lang a {
            display: flex;
            align-items: center;
            justify-content: center;
            font-size: 14px;
            color: #FFF;
            width: 32px;
            height: 32px;
            border-radius: 50%;
            background-color: $color-prim;
        }

        .close1 {

            img {
                filter: brightness(0) invert(1);
            }
        }
    }

    &>ul {
        display: flex;
        align-items: center;
        flex-direction: column;
        opacity: 0;
        transition: .5s;
        transition-delay: .5s;
        overflow-y: auto;

        &::-webkit-scrollbar {
            width: 8px;
        }

        &::-webkit-scrollbar-track {
            background-color: $color-sec;
        }

        &::-webkit-scrollbar-thumb {
            // border-radius: 10px;
            background-color: $color-prim;
        }

        @include mob480 {
            margin-top: 50px
        }

        &>li {
            position: relative;
            padding: 7px 0;
            // margin-bottom: 20px;
            transition: .5s;
            transform: translateX(-10px);

            @include LTR {
                transform: translateX(10px);
            }

            opacity: 0;

            &:nth-child(1) {
                transition-delay: .2s;
            }

            &:nth-child(2) {
                transition-delay: .4s;
            }

            &:nth-child(3) {
                transition-delay: .6s;
            }

            &:nth-child(4) {
                transition-delay: .8s;
            }

            &:nth-child(5) {
                transition-delay: 1s;
            }

            &:nth-child(6) {
                transition-delay: 1.2s;
            }

            &:nth-child(7) {
                transition-delay: 1.4s;
            }

            &:nth-child(8) {
                transition-delay: 1.6s;
            }

            &:nth-child(9) {
                transition-delay: 1.8s;
            }

            &:nth-child(10) {
                transition-delay: 2s;
            }

            &:nth-child(11) {
                transition-delay: 2.2s;
            }

            a {
                width: 100%;
                display: block;
                font-size: 18px;
                color: $color-white;
                transition: .5s;
            }

            i {
                position: absolute;
                top: 13px;
                left: 13px;
                transform: rotate(90deg);
                font-size: 9px;
                color: currentColor;
                transition: .5s;

                html[dir="ltr"] & {
                    left: unset;
                    right: 13px;
                }

                &.open {
                    transform: rotate(0deg);
                }
            }

            .drop {
                width: 100%;
                padding: 5px 15px;
                display: none;
                overflow: visible;
                height: auto;

                li {
                    padding: 4px 0;

                    a {
                        font-size: 15px;
                        color: #898989;
                        transition: .5s;

                        &:hover {
                            color: #595959;
                        }
                    }
                }
            }
        }
    }
}

// Start Header
header {
    .TopNav {
        padding-top: 30px;
        margin-bottom: 75px;

        .content {
            display: flex;
            align-items: center;
            justify-content: space-between;

            .logo {
                @include Sm {
                    display: flex;
                    align-items: center;
                    justify-content: space-between;
                    width: 100%;
                }

                svg {
                    display: none;
                    width: 30px;
                    height: 30px;

                    path {
                        fill: #7d4c21;
                        transform: scale(1.8);
                    }

                    @include Sm {
                        display: flex;
                        align-items: center;
                        justify-content: center;
                    }
                }
            }

            .links {
                display: flex;
                align-items: center;

                @include Sm {
                    display: none;
                }

                li {
                    &:not(:last-child) {
                        margin-right: 25px;

                        @include LTR {
                            margin-left: 25px;
                            margin-right: 0;
                        }
                    }

                    a {
                        font-size: 16px;
                        color: #000;
                    }

                    &.last {
                        margin-left: 90px;

                        @include LTR {
                            margin-right: 90px;
                            margin-left: 0;
                        }

                        width: 74px;
                        height: 40px;
                        border-radius: 100px;
                        background-color: #7d4c21;
                        @extend .Flex;
                        transition: 0.5s;

                        &:hover {
                            transform: translateY(-5px);
                        }

                        a {
                            @extend .Flex;
                            width: 1005;
                            height: 100%;
                            font-size: 16px;
                            color: #fff;
                        }
                    }
                }
            }
        }
    }

    >.container {
        position: relative;
        min-height: 100vh;
    }

    .slider {
        position: absolute;
        width: 100%;
        top: calc(50% + 17px);
        left: 50%;
        transform: translate(-50%, -50%);

        // .swiper-container {
        //     padding-bottom: 13px;
        // }

        .swiper-pagination {

            @include Sm {
                bottom: -40px;
                left: 15px;

                @include LTR {
                    right: 15px;
                    left: unset;
                }
            }

            span {
                width: 17px;
                height: 5px;
                border-radius: 3px;
                background-color: #7d4c21;
                transition: 0.5s;
                margin: 0 2.5px;

                &:focus {
                    outline: none;
                }

                &.pagination-active-bullet {
                    opacity: 1;
                    background-color: #bf8b4a;
                }
            }
        }

        .swiper-slide {
            text-align: left;

            @include LTR {
                text-align: right;
            }

            .row {
                @include mob480 {
                    margin: 0 !important;
                }
            }

            p {
                transform: translateX(15px);

                @include LTR {
                    transform: translateX(-15px);
                }

                opacity: 0;
                font-size: 18px;
                color: #000;
                line-height: 35px;
                width: 450px;
                margin-bottom: 20px;
                transition: 0.7s 1.4s;

                @include mob480 {
                    width: auto;
                }
            }

            h1 {
                transform: translateX(15px);

                @include LTR {
                    transform: translateX(-15px);
                }

                opacity: 0;
                font-size: 30px;
                font-family: bold;
                color: #7d4c21;
                position: relative;
                margin-bottom: 30px;
                transition: 0.7s 1.2s;

                &:after {
                    content: "";
                    display: block;
                    width: 24px;
                    height: 4px;
                    border-radius: 2px;
                    background-color: $color-sec;
                    margin-top: 10px;
                }

                @include mob480 {
                    font-size: 30px;
                }
            }

            a {
                transform: translateX(15px);

                @include LTR {
                    transform: translateX(-15px);
                }

                opacity: 0;
                @extend .Flex;
                width: 130px;
                height: 50px;
                background-color: $color-sec;
                color: #fff;
                border-radius: 100px;
                font-size: 18px;
                font-family: bold;
                transition: 0.7s 1.6s;
            }

            .inner-content {
                display: flex;
                flex-direction: column;
                justify-content: center;
            }

            .slider-pic-animate {
                position: relative;
                display: flex;
                justify-content: flex-end;
                align-items: center;
                filter: blur(5px);
                opacity: 0;

                img {
                    border-radius: 40px;
                }

                @include Sm {
                    display: none;
                }
            }

            &.swiper-slide-active {
                .pos {
                    transform: none !important;
                }

                .inner-content {
                    >* {
                        transform: none !important;
                        filter: none;
                        opacity: 1;
                    }

                    a {
                        filter: url("#goo");
                    }
                }

                .slider-pic-animate {
                    filter: none;
                    opacity: 1;
                    transition: 0.5s 0.4s;
                }
            }
        }
    }
}

