                    border-radius: 100px;
                    padding-left: 50px;
                    padding-right: 10px;
                    color: #000000;
                    font-size: 16px;

                    &::placeholder {
                        color: #000000;
                        font-size: 16px;
                        transition: .5s;
                    }

                    @include LTR {
                        padding-right: 50px;
                        padding-left: 10px;
                    }

                    &:focus {

                        &::placeholder {
                            color: rgba(#7D4C21, .3)
                        }
                    }
                }

                textarea {
                    border-radius: 25px;
                    padding-top: 12px;
                    resize: none;
                }

                &:last-of-type {
                    height: 90px;
                    margin-bottom: 20px;
                }

                img {
                    position: absolute;
                    top: 16px;
                    left: 20px;

                    @include LTR {
                        left: unset;
                        right: 20px;
                    }
                }
            }

            button {
                width: 100%;
                height: 50px;
                border-radius: 100px;
                background-color: #7D4C21;
                @extend .Flex;
                @extend .hover;
                font-size: 18px;
                color: #FFF;
                border: 0;
            }
        }
    }
}

// Faq

// Contact

.contact-sec {
    padding: 53px 0 57px;
    background-color: #1D1C1C;

    .col-lg-4 {

        &:not(:last-of-type) {
            margin-bottom: 30px;
        }
    }

    .box {
        width: 100%;
        display: flex;
        align-items: center;

        .pic {
            @extend .Flex;
            width: 90px;
            height: 90px;
            flex-shrink: 0;
            background-color: #643C19;
            border-radius: 50%;
            margin-right: 15px;

            @include LTR {
                margin-right: 0;
                margin-left: 15px;
            }

            img {
                transition: .5s;
            }
        }

        &:hover {

            .pic {

                img {
                    transform: rotateY(360deg)
                }
            }
        }

        .details {

            h3 {
                font-size: 18px;
                font-family: bold;
                color: #BF8B4A;
                margin-bottom: 10px;
            }

            a {
                font-size: 15.8px;
                color: #FFF;
                display: block;

                &:last-of-type {
                    margin-top: 5px;
                }
            }
        }
    }
}

// Contact

// Footer

footer {
    padding: 75px 0;
    background-image: url("../images/footer.jpg");
    background-repeat: no-repeat;
    background-position: bottom;
    background-size: cover;

    .logo {
        text-align: center;
    }

    .links {
        display: flex;
        align-items: center;
        justify-content: center;
        margin: 40px 0;

        @include Sm {
            flex-wrap: wrap;
            margin: 30px 0 40px;
        }

        li {

            &:not(:last-child) {
                margin-right: 25px;

                @include LTR {
                    margin-left: 25px;
                    margin-right: 0;
                }
            }

            @include Sm {
                margin-top: 10px;
            }

            a {
                color: #FFF;
                font-size: 16px;
            }
        }
    }

    .socials {
        display: flex;
        align-items: center;
        justify-content: center;

        a {
            &:not(:last-child) {
                margin-right: 6px;

                @include LTR {
                    margin-left: 6px;
                    margin-right: 0;
                }
            }

            svg path {
                transition: .5s;
            }

            &:hover {

                svg path {
                    fill: #7D4C21;
                }
            }
        }
    }
}

